@import 'reset.css';
body{
  background-color: #fff;
}
.root-wrap {
  position: absolute;
  top:0;
  bottom: 0;
  width: 100%;
}

.header-container {
  width: 100%;
  height: 80px;
  background-color: rgb(9, 9, 75);
}

.header-wrap {
  height: 100px;
  max-width: 1400px;
  margin: 0 auto;

  display: flex;
  align-items: center;
}

.header-left-wrap {
  display: flex;
  padding: 20px;
  margin-left: 150px;
}

.header-left-wrap ul {
  display: flex;
  margin-left: 16px;
}
.header-left-wrap li {
  margin-left: 14px;
  padding: 10px;
}

.header-nav-item{
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.page-container{
  max-width: 1400px;
  margin: 0 auto;
  padding: 32px 0;
}

.movies-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #292c4e;
  color: #fff;
  border-radius: 5px;
}

.movie_container {
  position: relative;
  width: 250px;
  margin: 16px;
  background-color: #373b69;
  color: #fff;
  border-radius: 5px;
}

.movie_container img {
  max-width: 100%;
}

.movie_info{
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.movie_info h4{
  margin: 0;
}

.detail_container {
  display: none;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; 
  height: 100%; 
}
.detail_info{
  width: 100px;
  height: 100px;
  /* display: flex; */
  /* justify-content: space-between; */
  text-align: center;
  align-items: center;
  /* position: absolute; */
}

.movie_container:hover .detail_container {
  display: block;
}

.LoginBox{
  margin-top: 50px;
  position: absolute;
  left: 50%;
  transform:translate(-50%,0);
  width: 600px;
  height: 500px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.inputWrap{
  border-radius: 8px;
  padding:16px;
  margin-top: 8px;
  background-color: #fff;
  border: 1px solid #c9c9c9;

}
.input {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  font-size: 16px;
}
.input::placeholder {
}
.inputWrap:focus-within {
  border: 2px solid #535782;
}
.contentWrap{
  width: 600px;
  height: 300px;
  margin-top: 40px;
  flex: 1;
}

.titleWrap {
  font-size: 36px;
}
.error{
  margin-top: 8px;
  color: red;
  font-size: 14px;
}
.bottom_btn{
  margin-top: 30px;
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 25px;
  background-color: #34349f;
  color: #fff;
  cursor: pointer;
}

.bottom_btn:disabled {
  background-color: #ccc;
}
